.language-select{position:relative;border-radius:6px;max-height:35px;width:fit-content;display:flex;min-height:31px}.language-select::after{content:"▼";color:#fff;position:absolute;right:12%;font-size:7px;top:50%;line-height:7px}.language-select svg{position:absolute;top:25%;left:12px}.language-select select{border:1px solid rgba(255,255,255,.5019607843);border-radius:5px;background:rgba(0,0,0,0);outline:none;padding:6px 36px 6px 32px;color:#fff;appearance:none}.language-select select:focus{outline:2px solid}.language-select select option{width:150px;background-color:#fff;color:#000}@media only screen and (max-width: 600px){.language-select::after{right:22%}.language-select select{font-size:0px}.language-select select option{font-size:18px;width:200px}}